@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
*{
  font-family: 'Roboto', sans-serif;
  box-sizing: border-box;
}

body{
  margin: 0;
}

#root{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  height: 100vh;
  -webkit-align-items: center;
          align-items: center;
  background-color: #eee;
}

h1, h2, h3, h4{
  margin: 5px 0;
}

.App{
  padding: 10px;
  border-radius: 5px;
  width: 300px;
  min-height: 75%;
  margin: 0 auto;
  /* border: 1px solid #333;
  box-shadow: 0 4px 15px #000;
  background-color: rgb(233, 233, 233); */
}

.comp{
  margin-bottom: 15px;
}


#InEx{
  background-color: #fff;
  border-radius: 5px;
  padding: 5px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: center;
          justify-content: center;
  box-shadow: 1px 1px 6px 0px #00000045;
}

#InEx div{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: 50%;
}

#InEx .gain{
  color: #6aaf26;
  font-weight: bold;
  font-size: 1.3em;
}

#InEx .pain{
  color: brown;
  font-weight: bold;
  font-size: 1.3em;
}

.in{
  border-right: 1px solid #333;
}

h3{
  padding-bottom: 5px;
  border-bottom: 2px solid rgb(190, 190, 190);
  margin-bottom: 15px;
}

ul{
  padding: 0;
}

.trans{
  list-style: none;
  box-shadow: 1px 1px 6px 0px #00000045;
  background-color: #fff;
  border-radius: 2px;
  margin-bottom: 5px;
  padding: 3px 5px;
  display: -webkit-flex;
  display: flex;
  position: relative;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.trans.add{
  border-right: 5px solid #6aaf26;
}

.trans.take{
  border-right: 5px solid brown;
}

#delete-btn{
  position: absolute;
  background-color: brown;
  color: #fff;
  font-weight: bold;
  padding: 3px;
  height: 20px;
  width: 20px;
  left:-20px;
  text-align: center;
  line-height: 16px;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.trans:hover #delete-btn{
  opacity: 1;
}

.add-trans input{
  width: 100%;
  border: 1px solid #999;
  border-radius: 2px;
  padding: 10px;
  margin-bottom: 10px;
  font-size: 16px;
}

.btn{
  width: 100%;
  background-color: cornflowerblue;
  border: 0;
  padding: 10px;
  text-align: center;
  font-size: 1.1em;
  font-weight: bold;
}

.more{
  border-right: 8px solid #6aaf26;
}

.less{
  border-right: 8px solid brown;
}
